var Cell, CellStore, Dispatcher, EventEmitter, GAME, GRID, INITIALIZED, Immutable, KeyDownAction, LAST_CELLS, LIVE_CELLS, Ticker, XY, ref;

KeyDownAction = require('../actions').KeyDownAction;

ref = require('../utility'), Ticker = ref.Ticker, XY = ref.XY, GAME = ref.GAME;

Cell = require('../views/cell');

GRID = require('../settings').GRID;

Dispatcher = require('../dispatcher');

EventEmitter = require('events').EventEmitter;

Immutable = require('immutable');

LAST_CELLS = null;

LIVE_CELLS = Immutable.OrderedMap().withMutations(function(mutable_cells) {
  var col, i, len, range, results, row;
  range = GRID.range();
  results = [];
  for (i = 0, len = range.length; i < len; i++) {
    row = range[i];
    results.push((function() {
      var j, len1, results1;
      results1 = [];
      for (j = 0, len1 = range.length; j < len1; j++) {
        col = range[j];
        results1.push(mutable_cells.set(XY.value_of(row, col), null));
      }
      return results1;
    })());
  }
  return results;
});

INITIALIZED = false;

CellStore = Object.create(EventEmitter.prototype, {
  initialize: {
    enumerable: true,
    value: function() {
      if (INITIALIZED) {
        throw new Error("CellStore already initialized");
      }
      this._reset();
      return Dispatcher.register((function(_this) {
        return function(action) {
          return _this._handle_action(action);
        };
      })(this));
    }
  },
  cellmap: {
    enumerable: true,
    value: function() {
      return LIVE_CELLS;
    }
  },
  add_change_listener: {
    enumerable: true,
    value: function(listener) {
      return this.addListener(this.CHANGE_EVENT, listener);
    }
  },
  CHANGE_EVENT: {
    enumerable: true,
    value: 'change'
  },
  METHOD_KEYMAP: {
    value: Immutable.Map([['restart', '__restart']])
  },
  _handle_action: {
    value: function(action) {
      var method, motion;
      if (action.is(KeyDownAction)) {
        motion = action.motion();
        method = action.method();
        if ((motion != null) && !GAME.over()) {
          GAME.set_motion(motion);
          if (!Ticker.ticking()) {
            return this._tick();
          }
        } else if (method != null) {
          return this[this.METHOD_KEYMAP.get(method)]();
        }
      }
    }
  },
  _emit_cells: {
    value: function() {
      return this.emit(this.CHANGE_EVENT, {
        cellmap: LIVE_CELLS
      });
    }
  },
  _tick: {
    value: function() {
      LAST_CELLS = LIVE_CELLS;
      this._update();
      if (GAME.out_of_bounds()) {
        GAME.collide(Cell.Wall);
      }
      if (GAME.over()) {
        this._finish();
      } else {
        Ticker.tick((function(_this) {
          return function() {
            return _this._tick();
          };
        })(this));
      }
      return this._emit_cells();
    }
  },
  _reset: {
    value: function() {
      var random_cell;
      random_cell = function() {
        var cell;
        cell = Cell.random();
        if (cell === Cell.Item) {
          GAME.add_item();
        }
        return cell;
      };
      GAME.reset();
      this._batch_mutate(function(mutable_cells) {
        return mutable_cells.forEach(function(cell, xy) {
          if (GAME.collision(xy)) {
            return mutable_cells.set(xy, Cell.Snake);
          } else {
            return mutable_cells.set(xy, random_cell());
          }
        });
      });
      return this._emit_cells();
    }
  },
  _finish: {
    value: function() {
      var transform_to_cell;
      if (GAME.failed()) {
        transform_to_cell = Cell.Collision;
        this._rewind();
      } else {
        transform_to_cell = Cell.Item;
      }
      return this._batch_mutate(function(mutable_cells) {
        return mutable_cells.forEach(function(cell, xy) {
          if (cell === Cell.Snake) {
            return mutable_cells.set(xy, transform_to_cell);
          }
        });
      });
    }
  },
  _batch_mutate: {
    value: function(mutator) {
      return LIVE_CELLS = LIVE_CELLS.withMutations(mutator);
    }
  },
  _rewind: {
    value: function() {
      LIVE_CELLS = LAST_CELLS;
      return LAST_CELLS = null;
    }
  },
  _update: {
    value: function() {
      GAME.move_snake();
      return this._batch_mutate((function(_this) {
        return function(mutable_cells) {
          return mutable_cells.forEach(function(previous_cell, xy) {
            if (GAME.collision(xy)) {
              if (previous_cell === Cell.Void) {
                return mutable_cells.set(xy, Cell.Snake);
              } else {
                if (previous_cell === Cell.Item) {
                  mutable_cells.set(xy, Cell.Snake);
                }
                return GAME.collide(previous_cell, xy);
              }
            } else if (previous_cell === Cell.Snake) {
              return mutable_cells.set(xy, Cell.Void);
            }
          });
        };
      })(this));
    }
  },
  __restart: {
    value: function() {
      return Ticker.stop((function(_this) {
        return function() {
          return _this._reset();
        };
      })(this));
    }
  }
});

module.exports = CellStore;
