var Cells, Immutable, Random, _,
  slice = [].slice;

Immutable = require('immutable');

_ = require('underscore');

Cells = require('../views/cell/types');

Random = require('../utility/random');

module.exports = {
  setting: Immutable.Map([[Cells.Wall, Immutable.Range(0, 15)], [Cells.Item, Immutable.Range(40, 55)]]),
  choose: function() {
    var cells, sample;
    cells = 1 <= arguments.length ? slice.call(arguments, 0) : [];
    sample = Random.int(0, 1000);
    return _.find(cells, (function(_this) {
      return function(cell) {
        return _this.setting.get(cell).contains(sample);
      };
    })(this));
  }
};
