var Cell, Immutable, LEVEL, React, _, cells;

React = require('react');

cells = require('./types');

LEVEL = require('../../settings').LEVEL;

Immutable = require('immutable');

_ = require('underscore');

Cell = React.createClass({
  propTypes: {
    content: React.PropTypes.oneOf(_.values(cells)).isRequired
  },
  statics: _.extend({}, cells, {
    classmap: Immutable.Map([[cells.Item, 'item-cell'], [cells.Void, 'void-cell'], [cells.Wall, 'wall-cell'], [cells.Snake, 'snake-cell'], [cells.Collision, 'collision-cell']]),
    random: function() {
      var choice;
      choice = LEVEL.choose(cells.Wall, cells.Item);
      return ((choice != null) && choice) || this.Void;
    }
  }),
  render: function() {
    return React.createElement("div", {
      "className": "" + (this.constructor.classmap.get(this.props.content))
    });
  }
});

module.exports = Cell;
