var CellStore, GRID, Grid, React, Row;

React = require('react');

Row = require('../row');

GRID = require('../../settings').GRID;

CellStore = require('../../stores').CellStore;

Grid = React.createClass({
  getInitialState: function() {
    return {
      cellmap: CellStore.cellmap()
    };
  },
  componentDidMount: function() {
    return CellStore.add_change_listener(this._on_change);
  },
  _on_change: function() {
    return this.setState({
      cellmap: CellStore.cellmap()
    });
  },
  _get_row_cells: function(row) {
    return this.state.cellmap.filter(function(cell, xy) {
      return xy.x === row;
    }).valueSeq().toJS();
  },
  render: function() {
    var row;
    return React.createElement("div", {
      "className": "grid"
    }, (function() {
      var i, len, ref, results;
      ref = GRID.range();
      results = [];
      for (i = 0, len = ref.length; i < len; i++) {
        row = ref[i];
        results.push(React.createElement(Row, {
          "cells": this._get_row_cells(row),
          "index": row,
          "key": "row-" + row
        }));
      }
      return results;
    }).call(this));
  }
});

module.exports = {
  mount: function(_html_element) {
    this._html_element = _html_element;
    return this;
  },
  render: function(props) {
    var __GRID__;
    if (this._html_element == null) {
      throw new Error("Set HTMLElement html_element before rendering!");
    } else if (typeof __GRID__ !== "undefined" && __GRID__ !== null) {
      throw new Error("Grid's already been rendered!");
    }
    return __GRID__ = React.render(React.createElement(Grid, React.__spread({}, props)), this._html_element);
  }
};
