var Cell, GAME_OVER, GRID, Immutable, Snake, XY;

Snake = require('../snake');

GRID = require('../settings').GRID;

Cell = require('../views/cell');

XY = require('../utility/xy');

Immutable = require('immutable');

GAME_OVER = {
  failure: {
    success: false
  },
  success: {
    success: true
  }
};

module.exports = {
  reset: function() {
    this._snake = Snake.at_position(XY.random(GRID.dimension - 1));
    this._game_over = null;
    return this._num_items = 0;
  },
  collision: function(xy) {
    return this._snake.meets(xy);
  },
  collide: function(target, xy) {
    if (target === Cell.Wall) {
      return this._fail();
    } else if (target === Cell.Snake && this._snake.head() === xy) {
      return this._fail();
    } else if (target === Cell.Item) {
      return this._score();
    }
  },
  set_motion: function(motion) {
    return this._snake.set_motion(motion);
  },
  move_snake: function() {
    return this._snake.move();
  },
  out_of_bounds: function() {
    var ref, x, y;
    ref = this._snake.head(), x = ref.x, y = ref.y;
    return Math.min(x, y) < 0 || Math.max(x, y) >= GRID.dimension;
  },
  add_item: function() {
    return this._num_items += 1;
  },
  over: function() {
    return this._game_over != null;
  },
  failed: function() {
    if (this._game_over != null) {
      return !this._game_over.success;
    } else {
      return false;
    }
  },
  _score: function() {
    this._snake.grow();
    this._num_items -= 1;
    if (this._num_items === 0) {
      return this._game_over = GAME_OVER.success;
    }
  },
  _fail: function() {
    this._snake.set_motion(null);
    return this._game_over = GAME_OVER.failure;
  }
};
