var $, CellStore, Grid, KeyDownAction, KeySender, ROOT, ref;

CellStore = require('./stores').CellStore;

ref = require('./actions'), KeySender = ref.KeySender, KeyDownAction = ref.KeyDownAction;

Grid = require('./views');

$ = window.$;

if ($ != null) {
  ROOT = $('#mamba')[0];
  KeySender.initialize({
    jQuery: $
  }).listen(KeyDownAction.KEYCODES, {
    prevent_default: true
  });
  CellStore.initialize();
  Grid.mount(ROOT).render();
} else {
  throw new Error("Couldn't find window.$, are you sure jQuery is loaded?");
}
